body {
	margin: 0;
}

.sidebar {
	height: 100vh;
	background-color: rgb(14, 47, 110);
	color: white;
	font-size: 20px;
	padding: 0 40px;
	display: flex;
	top: 0;
	position: sticky;
	flex-direction: column;
	span:first-child {
		margin-top: 150px;
	}
	span {
		margin: 5px 0;
		cursor: pointer;
	}
}

.catContainer {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	justify-items: center;
	margin: 0 auto;

	@media only screen and (max-width: 900px) {
		grid-template-columns: 1fr 1fr;
	}
	@media only screen and (max-width: 655px) {
		grid-template-columns: 1fr;
	}

	span {
		margin: 15px;
		width: 200px;
		height: 200px;
	}

	img {
		width: 100%;
		height: 100%;
	}
}

.no-category {
	margin: 150px auto;
	font-size: 30px;
}

.main-wrap {
	margin: 0 auto;
	text-align: center;
	button {
		width: 50%;
		margin: 0 auto 50px;
		height: 50px;
	}
}
